import img from './../assets/avaImg/nina.jpg';
import img1 from './../assets/avaImg/rick.jpg';
import img2 from './../assets/avaImg/emma.jpg';
import img3 from './../assets/avaImg/movie.jpg';

const ADD_MESSAGE = 'ADD_MESSAGE';

let initial = {
  messagesList: [
      {id: '1', dialogname: 'Александр', img: null},
      {id: '2', dialogname: 'Нина', img: img},
      {id: '3', dialogname: 'Лена', img: null},
      {id: '4', dialogname: 'Дана', img: img3},
      {id: '5', dialogname: 'Мэдисон', img: null},
      {id: '6', dialogname: 'Абрахам', img: img1},
      {id: '7', dialogname: 'Николас', img: null},
      {id: '8', dialogname: 'Пиральта', img: img2}
  ],
  dialog: [
      {id: '0', message: 'сообщение'},
      {id: '1', message: 'еще сообщение'},

  ]
}

let messagesReducer = (state = initial, action) => {
    switch (action.type) {
        case ADD_MESSAGE:
          return {
            ...state,
            dialog: [...state.dialog, {id: '4', message: action.messageBody}]
          }
        default:
            return state;
    }
};

export let addMessageAC = (messageBody) => {
  return {type: ADD_MESSAGE, messageBody}
}
 
export default messagesReducer;

